//////MY++
    onChangeDiskFrom(event) {
        let val = event.target.value.replace(/[^0-9]/g, "");
        val = parseInt(val);
        this.setState({diskvalue_from: val});
        if ( this.state.diskvalue_to < val ) this.setState({diskvalue_to: val});
    }
    onChangeDiskTo(event) {
        let val = event.target.value.replace(/[^0-9]/g, "");
        val = parseInt(val);
        this.setState({diskvalue_to: val});
        if ( this.state.diskvalue_from > val ) this.setState({diskvalue_from: val});
    }

	checkDiscTo(value)
	{
		if (this.state.diskvalue_to < value) {this.setState({ diskvalue_to: parseInt(value) })}
	}

	checkDiscFrom(value)
	{
		if (this.state.diskvalue_from > value)
		{
			this.setState({ diskvalue_from: parseInt(value) })
		}
	}

	checkRamTo(value)
	{
		if (this.state.ramvalue_to < value) {this.setState({ ramvalue_to: parseInt(value) })}
	}

	checkRamFrom(value)
	{
		if (this.state.ramvalue_from > value) {this.setState({ ramvalue_from: parseInt(value) })}
	}

	checkCpuCountTo(value)
	{
		if (this.state.cpucount_to < value) {this.setState({ cpucount_to: parseInt(value) })}
	}

	checkCpuCountFrom(value)
	{
		if (this.state.cpucount_from > value) {this.setState({ cpucount_from: parseInt(value) })}
	}

	checkPriceTo(value)
	{
		if (this.state.price_to < value) {this.setState({ price_to: parseInt(value) })}
	}

	checkPriceFrom(value)
	{
		if (this.state.price_from > value) {this.setState({ price_from: parseInt(value) })}
	}



